:root {
    --ff-primary: 'Nunito', sans-serif;
    --ff-secondary: 'Roboto', sans-serif;
    --clr-font1: #000000;
    --clr-font2: #ffffff;
    --clr-accent: #f73859;

}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #ffffff;
  /* background-color: var(--clr-main_bg); */
}

h2 {
  color: #000000;
}

:root {
    --ff-primary: 'Nunito', sans-serif;
    --ff-secondary: 'Roboto', sans-serif;
    --ff-italic: 'Sansita', sans-serif;
    --ff-bilbo: 'Bilbo Swash Caps', cursive;
    --ff-titles: 'Bebas Neue', cursive;

    --fw-reg: 300;
    --fw-bold: 900;

    --clr-font1: #000000;
    --clr-font2: #ffffff;
    --clr-accent: #f73859;
    --clr-dark: #010606;
    --clr-light: #f7385880;
    --clr-sub: #ededed;

}


.slider {
    position: relative;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    height: 600px;
}
  
  .right-arrow {
    position: absolute;
    top: 50%;
    right: 32px;
    font-size: 3rem;
    color: #ededed;
    color: var(--clr-sub);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: #ededed;
    color: var(--clr-sub);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
  }
  
  .slide {
    opacity: 0;
    transition-duration: 1s ease;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1s;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }


